<template>
    <div id="app">
        <header>
            <h1>연세직업전문학교</h1>
        </header>
        <main>
            <section class="intro">
                <p>
                    연세직업전문학교에 오신 것을 환영합니다. <br>
                    이제 혁신적인 도전과 함께 여러분의 꿈을 실현하세요! <br>
                    풀스택 개발 전문가로 성장할 수 있는 특별한 기회, <br>
                    지금 바로 시작하세요!
                </p>
            </section>
            <section class="features">
                <!-- 반복 feature -->
                <div class="feature" v-for="feature in features" :key="feature.id">
                    <div class="image-container">
                        <a href="#">
                            <img :src="feature.image" :alt="feature.title" class="feature-image">
                        </a>
                    </div>
                    <!-- 데이터바인딩 사용하여 feature 객체의 title 속성 렌더링-->
                    <h2>{{ feature.title }}</h2>
                    <p>{{ feature.description }}</p>
                    <button class="apply-button" @click="apply(feature.id)">
                        <i class="fa-solid fa-pen"></i> {{ applyText }}
                    </button>
                    
                </div>
            </section>
            
            <!-- 수강신청폼 -->
             <section class="apply-form">
                <h2>수강신청하기</h2>
                <!-- 이벤트 발생 시 기본 동작 막고 지정한 메서드 submitForm 실행되도록 함 -->
                <form @submit.prevent="submitForm">
                    <div class="form-group">
                        <label for="name"> 이름 </label>
                         <!-- v-model="form.name" 양방향 데이터바인딩
                         사용자가 입력한 값이 폼 네임에 자동저장 -->
                        <input type="text" id="name" v-model="form.name" required />   
                    </div>
                    <div class="form-group">
                        <label for="name"> 이메일 </label>
                        <input type="email" id="email" v-model="form.email" required />   
                    </div>
                    <div class="form-group">
                        <label for="name"> 수강반 </label>
                        <select id="class" v-model="form.classId" required>
                            <option :value="feature.id" v-for="feature in features" :key="feature.id"> {{ feature.title }} </option>
                        </select> 
                    </div>
                    <button type="submit" class="submit-button">신청하기</button>
                </form>
             </section>
        </main>
        <footer>
            <p>
                &copy; 2024 연세직업전문학교. All rights reserved.
            </p>
        </footer>
    </div>
</template>

<script>
    export default {
        name : "App",
        data(){
            return {
                features: [
                    {
                        id: 1,
                        title: "AI 풀스택반",
                        discription: "인공지능 기술을 배우고, 실무 능력을 키워보세요.",
                        image: "images/ai.png",
                    },
                    {
                        id: 2,
                        title: "프론트앤드반",
                        discription: "인공지능 기술을 배우고, 실무 능력을 키워보세요.",
                        image: "images/frontend.png",
                    },
                    {
                        id: 3,
                        title: "UI디자인반",
                        discription: "인공지능 기술을 배우고, 실무 능력을 키워보세요.",
                        image: "images/uidesign.png",
                    }
                ],
                applyText: '수강신청',
                form: {
                    name: '', // 사용자 이름 지정 변수 (초깃값 빈 문자열)
                    email: '', // 사용자 이메일 지정 변수 (초깃값 빈 문자열)
                    classId: null, // 사용자가 선택한 클래스 ID 지정 변수 (초깃값 null)
                }
            };
        },
      methods: {
        apply(classId) {
            alert(`수강신청이 완료되었습니다. 클래스 ID ${classId}`);
        },
        submitForm(){
            alert(`수강신청이 완료되었습니다\n이름: ${this.form.name}\n이메일: ${this.form.email}\n수강반: ${this.form.classId}`);
        }
      }  
    };
</script>

<style>
*{
    margin:0;
    padding:0;
}
#app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: 'Nanum Gothic';
    background-color: #fff;
}
header {
    background-color: #004c98;
    color: #fff;
    text-align: center;
    /* root em의 약자. 기본크기 16px이므로 1rem=16px */
    padding: 1rem 0;
}
main {
    display: flex;
    flex: 1; /* 공간을 균등하게 나누어 차지 */
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
}
section.intro {
    text-align: center;
}
section.features {
    display: grid;
    gap: 1rem;
    /* auto-fit - 가능한 공간에 맞춰 열을 자동으로 배치 */
    /* minmax - 각 열의 최소 크기를 250으로 설정 */
    /* 1fr - 사용 공간을 균들하게 나눔 */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
section.features .feature {
    border: 1px solid #ccc;
    padding: 1rem;
    border-radius: 5px;
    background-color: #f9f9f9;
    text-align: center;
}
section.features .feature .image-container {
    width: 100%;
    height: 200px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
section.features .feature .image-container .feature-image {
    width: 100%;
    height: auto;
    object-fit: cover; /* 이미지 크기를 컨테이너에 맞게 조정 */
}
section.features .feature .apply-button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #004c98;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
section.features .feature .apply-button:hover {
    background-color: #003366;
}
h2 {
    margin-top: 20px;
}
footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 1rem 0;
}


/* 스타일 추가 */
.apply-form {
  margin-top: 2rem;
  padding: 2rem;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.apply-form h2 {
  margin-bottom: 1rem;
  text-align: center;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 10px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.submit-button {
  padding: 0.75rem 1.5rem;
  background-color: #004c97;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #003366;
}

</style>